import {loginFn , registerFn} from '@/api/login'
import {getLocalStorage,setLocalStorage} from '@/utils/auth'

const user = {
	state: {
		userInfo: '',
		hasLogin: false,
		perms: [],
	},
	
	mutations: {
		SET_USERINFO: (state, userInfo) => {
			state.userInfo = userInfo
		},
		SET_PERMS: (state, perms) => {
			state.perms = perms
		},
		SET_HASLOGIN: (state, hasLogin) => {
			state.hasLogin = hasLogin
		}
	},
	
	actions: {
		loginByUsername({ commit }, userInfo) {
			return new Promise((resolve, reject)=>{
				loginFn(userInfo).then(res=>{
					if(res&&res.data){
						const perms = res.data.permission
						commit('SET_USERINFO', res.data)
						commit('SET_PERMS', perms)
						commit('SET_HASLOGIN', true)
						setLocalStorage(res.data)
						resolve()
					}
				}).catch(error=>{
					reject(error)
				})
			})
		},
		
		updateUserInfo({commit,state}) {
			// return new Promise((resolve,reject)=>{
			const userInfo = getLocalStorage()
			if(userInfo){
				commit('SET_USERINFO', userInfo)
				commit('SET_PERMS', userInfo.permission)
				commit('SET_HASLOGIN', true)
				// resolve()
			}
			// })
		}
	},
}

export default user


