import Vue from 'vue'
import Router from 'vue-router'
import routeJson from './router.json'
import Layout from '@/views/layout/layout'

Vue.use(Router)

export const constartRouterMap = [
	{
		path: '/login',
		component: () => import('@/views/login/index'),
		hidden: true
	},
	{
		path: '',
		component: () => import('@/views/dashboard/index')
	},
	{
		path: '/401',
		component: () => import('@/views/nopromise/index'),
		hidden: true
	}
]

export default new Router({
	scrollBehavior: () => ({ y: 0}),
	routes: constartRouterMap
})

const parseJson = () =>{
	const mapArray = routeJson
	for(let i = 0;i<mapArray.length;i++){
		const item = mapArray[i]
		item.component = Layout
		if(item.children && item.children.length>0){
			for(let j = 0;j<item.children.length;j++){
				const childrenItem = item.children[j]
				childrenItem.component = () => import(`@/views${childrenItem.page}`)
			}
		}
	}
	console.log(mapArray)
	return mapArray
}

export const asyncRouterMap = parseJson()


