import router from './router'
import {asyncRouterMap} from '@/router'
import {setLocalStorage} from '@/utils/auth'
import store from './store'
import { Message, MessageBox } from 'element-ui'


console.log(asyncRouterMap)
router.addRoutes(asyncRouterMap)


function hasPermission(perms,permissions) {
	console.log(perms)
	console.log(permissions)
	if(!permissions) return true
	if(!perms) return false
	return perms.some(perm => permissions.indexOf(perm) >= 0)
}

router.beforeEach((to,from,next)=>{
	//第一次加放到store/permission中，通过这个来判断有没有
	//如果没有登录，就继续走，此时只能进入router/index/js中的路由页面，如果登录了，根据返回的权限与asyncRouterMap对比，查看store.permission，在store.permission把路由菜单存入store,在本页面动态添加路由
	//v-permission还需要研究一下
	console.log(to)
	console.log(router)
	
	console.log(store.state.user)
	
	if(!store.state.user.hasLogin){
		store.dispatch('updateUserInfo')
		// router.addRoutes(array)
	}
	
	// next()
	// if(to.path=='/category/category'&&a==1){
	// 	console.log('yizhi0')
	// 	router.addRoutes(array)
	// 	console.log(router)
	// 	a++
	// 	next({...to, replace: true})
	// }else {
		
	// 	console.log('yizhi1')
		if(hasPermission(store.state.user.perms, to.meta.perms)){
			next()
		} else {
			next({ path: '/401' , replace: true, query: {noGoBack: true}})
		}
	// 	next()
	// }
	
})


